.navigation-section{border:.5px solid var(--black);border-left:0;border-right:0;padding:28px 0;position:sticky;top:0;z-index:9998;transition:top .3s;background-color:var(--white);width:100%;scroll-behavior:smooth}.navigation-section .container{display:flex;align-items:center;justify-content:center;padding:0}.navigation-section-wrapper{min-height:83px}.navigation__list{list-style-type:none;display:flex;align-items:center;gap:70px;overflow-x:auto;white-space:nowrap;padding:0;margin:0;scrollbar-width:none;-ms-overflow-style:none}.navigation__list::-webkit-scrollbar{display:none}.navigation__item{flex-shrink:0}.navigation__item:first-child{padding-left:22px}.navigation__item:last-child{padding-right:22px}.navigation__link{font-size:16px;font-weight:300;line-height:26px;letter-spacing:.3em;text-transform:uppercase;text-align:center;white-space:nowrap;padding:10px 0;display:inline-block;padding:0;margin:0}.navigation__link.active{font-weight:600}.navigation__link:hover{color:var(--dark-silver)}.navigation-section.scroll-down{position:fixed}.navigation-section.scroll-up{position:fixed}@media(max-width: 750px){.navigation-section{padding:10px 0}.navigation-section-wrapper{min-height:47px}.navigation__list{gap:30px}.navigation__link{font-size:11px}}